import styled from "styled-components";
import { Container } from "../../layout/Container/Container";

export const StyledSection = styled.section`
  padding-top: 183px;
  padding-bottom: 183px;
  position: relative;
  z-index: 1;
  min-height: 600px;
  background-color: #d8ecfe;
`;

export const StyledContainer = styled(Container)`
  position: relative;

&::before {
  content: '';
  position: absolute;
  right: 177px;
  bottom: -183px;
  z-index: 5;
  display: block;
  width: 273px;
  height: 627px;
  background-position: right bottom;
  background-repeat: no-repeat;
  background-image: url('data:image/svg+xml;base64,');
}

&::after {
  content: '';
  position: absolute;
  right: 90px;
  bottom: -106px;
  display: block;
  width: 446px;
  height: 447px;
  background-repeat: no-repeat;
  background-image: url('data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iNDQ2IiBoZWlnaHQ9IjQ0NyIgdmlld0JveD0iMCAwIDQ0NiA0NDciIGZpbGw9Im5vbmUiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+CjxlbGxpcHNlIGN4PSIyMjMiIGN5PSIyMjMuNSIgcng9IjIyMyIgcnk9IjIyMy41IiBmaWxsPSIjQzRFMkZGIi8+Cjwvc3ZnPgo=');
}
`;

export const StyledWrapper = styled.div`
  max-width: 600px;
`;

export const StyledText = styled.p`
  margin-top: 24px;
  padding-right: 110px;
  font-size: 18px;
  line-height: 150%;
`;


